@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:ital,wght@0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,small,strong,sub,sup,var,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,figcaption,figure,footer,header,hgroup,menu,nav,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;outline:0;font-size:100%;vertical-align:baseline;background:transparent}img{object-fit:contain}fieldset,form,h3{-webkit-margin-start:0 !important;margin-inline-start:0 !important;-webkit-margin-end:0 !important;margin-inline-end:0 !important;-webkit-padding-start:0 !important;padding-inline-start:0 !important;-webkit-padding-end:0 !important;padding-inline-end:0 !important;-webkit-padding-before:0 !important;padding-block-start:0 !important;-webkit-padding-after:0 !important;padding-block-end:0 !important}body{line-height:1}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}nav ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}a{margin:0;padding:0;font-size:100%;vertical-align:baseline;background:transparent}ins{background-color:#ff9;color:#000;text-decoration:none}mark{background-color:#ff9;color:#000;font-style:italic;font-weight:bold}del{text-decoration:line-through}abbr[title],dfn[title]{border-bottom:1px dotted;cursor:help}table{border-collapse:collapse;border-spacing:0}hr{display:block;height:1px;border:0;border-top:1px solid #cccccc;margin:1em 0;padding:0}input,select{vertical-align:middle}body{background-color:#f2f2f2}#root{display:flex;flex-direction:column;justify-content:center;align-content:center;width:100%}.App{display:flex;flex-direction:column;justify-content:center;margin:0 auto;max-width:50vw;text-align:center}.App .budget-input{display:flex;flex-direction:column;justify-content:center;align-content:center;margin:2vmin;padding:3vmin;border-radius:10px;background-color:#0568a6}.App .budget-input label{margin:2vmin 0 0 0;font-size:calc(18px + (32 - 18) * ((100vw - 300px) / (1800 - 300)));line-height:calc(22px + (36 - 22) * ((100vw - 300px) / (1800 - 300)));color:#fff}.App .budget-input input{margin:1vmin auto;padding:1vmin 2vmin;font-size:calc(18px + (32 - 18) * ((100vw - 300px) / (1800 - 300)));line-height:calc(22px + (36 - 22) * ((100vw - 300px) / (1800 - 300)));border-radius:10px}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.header-text{font-family:'Noto Serif', serif;font-size:5rem}.expense-savings{height:100px;background-color:green}input[type="text"]{width:12rem;color:red}

